<template>
<b-container>
  <b-row>
    <b-col>
      <b-form @submit.prevent="onSubmit" class="my-3">
        <b-form-input class="my-1 betterText" v-model="infohash" placeholder="enter the infohash to lookup"></b-form-input>
        <b-button type="submit" class="my-1">Look Up</b-button>
      </b-form>
      <p v-if="infohashMessage">{{infohashMessage}}</p>
    </b-col>
  </b-row>
  <b-row>
    <b-col>
      <!-- <b-row v-for="(peer, index) in peers" :key="index"> -->
        <p v-for="(data, index) in peers" :key="index">host: {{data.peer}} - infohash: {{data.infohash}}</p>
      <!-- </b-row> -->
    </b-col>
  </b-row>
</b-container>
</template>

<script>
import axios from 'axios'
import {Buffer} from 'buffer'
export default {
  name: 'Home',
  data(){
    return {
      infohash: '',
      host: window.location.host,
      hostname: window.location.hostname,
      infohashMessage: '',
      web: null,
      peers: [],
      comm: null
    }
  },
  methods: {
    getComm(){
      axios.get('http://' + this.host + '/comm').then(res => {
        this.comm = res.data
        this.getPeers()
      }).catch(error => {
        console.log(error)
      })
    },
    getPeers(){
      this.web = new WebSocket('ws://' + this.hostname + ':' + this.comm.ws)
      // console.log(this.web)
      this.web.onopen = event => {
        console.log('websocket opened\n' + event)
      }
      this.web.onclose = event => {
        console.log('websocket closed\n' + event)
      }
      this.web.onerror = error => {
        console.log('websocket error\n' + error)
      }
      this.web.onmessage = message => {
        let data = JSON.parse(message.data)
        if(data !== 'close'){
          let useData = {}
          useData.peer = 'http://' + data.peer.host + ':' + data.peer.port
          useData.infohash = Buffer.from(data.infoHash).toString('hex')
          this.peers.push(useData)
        } else {
          this.web.close()
        }
      }
    },
    onSubmit(){
      if(this.infohash){
        this.sendInfohash()
      }
    },
    sendInfohash(){
      axios.post('http://' + this.host + '/lookup', {hash: this.infohash}).then(res => {
        this.infohashMessage = 'looking up now, the peers will show as they are retrieved. if peers are not shown, then there is no one who is sharing that infohash'
        this.infohash = ''
        setTimeout(() => {this.infohashMessage = ''}, 10000)
      }).catch(error => {
        console.log(error)
        this.infohashMessage = 'there was an error'
        this.infohash = ''
        setTimeout(() => {this.infohashMessage = ''}, 10000)
      })
    }
  },
  created(){
    this.getComm()
  },
  beforeDestroy(){
    this.web.send(JSON.stringify('close'))
    this.web.close()
  }
}
</script>

<style scoped>
.betterText {
  text-align: center;
  font-weight: bold;
}
</style>